<template>
  <div class="index-backg">
    <!-- HeaderÂçÄÂ°äÔºåÂõ∫ÂÆöÂú®ÊúÄ‰∏äÊñπ -->
    <div class="header-wrapper">
      <AppHeader :cartItemCount="cartQuantity" @OpenSidebar="toggleSidebar" />
    </div>

    <!-- È°ØÁ§∫ÁãÄÊÖãÊèêÁ§∫ -->
    <div v-if="isShowToast" class="toast">
      <p>{{ toastMessage }}</p>
    </div>

    <!-- Âè≥ÂÅ¥ÂÅ¥Ê¨Ñ -->
    <div class="sidebar" :class="{ active: isSidebarOpen }">
      <!-- ÂÅ¥Ê¨ÑÊ®ôÈ°åËàáÈóúÈñâÊåâÈàï -->
      <div class="sidebar-header">
        <span class="sidebar-title">Ë≥ºÁâ©Ëªä</span>
        <button class="close-btn" @click="toggleSidebar">√ó</button>
      </div>
      <!-- Ë≥ºÁâ©ËªäÂÖßÂÆπ -->
      <div class="sidebar-content">
        <CartPage :cart="cart" @update-cart="updateCart" />
      </div>
    </div>

    <!-- BreadcrumbsÂçÄÂ°ä -->
    <!-- <div class="Breadcrumbs">
      <Breadcrumbs />
    </div> -->

    <!-- ‰∏ªË¶ÅÂÖßÂÆπÂçÄÂ°ä -->
    <div class="container-fluid">
      <router-view :cart="cart" @add-to-cart="addToCart" @update-cart="updateCart" />
    </div>

    <!-- FooterÂçÄÂ°ä -->
    <footer class="footer">
      <p>Êú¨Á∂≤Á´ôÂÉÖ‰æõÂÄã‰∫∫‰ΩúÂìÅ‰ΩøÁî®ÔºåÊú™Êèê‰æõÂïÜÊ•≠Áî®ÈÄî</p>
      <p>BBS &copy; 2025 COPYRIGHT</p>
    </footer>
  </div>
</template>

<script>
import { ref,computed, onMounted } from 'vue';
import { useStore } from 'vuex'; // ÂºïÂÖ• vuex ÁöÑ useStore
import AppHeader from './components/AppHeader.vue';
import CartPage from './views/CartPage.vue';  // ÂºïÂÖ• CartPage.vue
import { newsData } from './newsData.js'; // ÂºïÂÖ•ÂÖ±‰∫´Ë≥áÊñô
import { menuData } from './menuData.js'; // ÂºïÂÖ•ÂÖ±‰∫´Ë≥áÊñô

export default {
  components: { AppHeader, CartPage },
  setup() {
    const store = useStore(); // ‰ΩøÁî® store
    const isSidebarOpen = ref(false); // Áî®‰æÜÊéßÂà∂ÂÅ¥Ê¨ÑÊòØÂê¶ÈñãÂïü
    const isShowToast = ref(false);
    const toastMessage = ref('');

    // **ËºâÂÖ•ÊôÇËÆÄÂèñ localStorage ‰∏¶Êõ¥Êñ∞ Vuex Ë≥áÊñô**
    onMounted(() => {
      const savedCart = localStorage.getItem('cart');
      if (savedCart) {
        store.commit('setCart', JSON.parse(savedCart));
      }
    });

    // ‰ΩøÁî® computed ‰æÜË®àÁÆó cart ‰∏≠ÂïÜÂìÅÁöÑÁ∏ΩÊï∏Èáè
    const cartQuantity = computed(() => store.getters.cartQuantity);

    // **Âä†ÂÖ•Ë≥ºÁâ©Ëªä‰∏¶Â≠òÂÖ• `localStorage`**
    const addToCart = (item) => {
      store.commit('addToCart', item);
      localStorage.setItem('cart', JSON.stringify(store.state.cart));
      toastMessage.value = `${item.name} Â∑≤ÊàêÂäüÂä†ÂÖ•Ë≥ºÁâ©ËªäÔºÅ`; // Ë®≠ÂÆöÊèêÁ§∫Ë®äÊÅØ
      isShowToast.value = true; // È°ØÁ§∫ÊèêÁ§∫

      // 3ÁßíÂæåËá™ÂãïÈö±ËóèÊèêÁ§∫
      setTimeout(() => {
        isShowToast.value = false;
      }, 2000);
    };

    const updateCart = (updatedCart) => {
      store.commit('updateCart', updatedCart);
      localStorage.setItem('cart', JSON.stringify(updatedCart));
    };

    const toggleSidebar = () => {
      isSidebarOpen.value = !isSidebarOpen.value; // ÊéßÂà∂ÂÅ¥Ê¨ÑÈñãÈóú
    };

    return { cart: store.state.cart, addToCart, updateCart, toggleSidebar, isSidebarOpen, cartQuantity,isShowToast, toastMessage };
  },
  provide() {
    return {
      newsData: ref(newsData), // Êèê‰æõ newsData
      menuData: ref(menuData), // Êèê‰æõ menuData
    };
  },
};
</script>