<template>
  <div class="cart-container">
    <div v-if="cart.length === 0" class="cart-empty">
      <router-link :to="'/menu'" class="cta-button">ÈÅ∏Ë≥ºÂéª!</router-link>
    </div>
    <ul class="cart-items">
      <li v-for="(item, index) in cart" :key="item.id" class="cart-item">
        <img :src="item.image" class="cart-img" />
        <span class="item-info">
          {{ item.name }} - {{ item.price }} NTD x {{ item.quantity }} = {{ item.price * item.quantity }} NTD
          <div class="quantity-controls">
            <button class="p-adj" @click="updateQuantity(index, item.quantity - 1)">‚óÄ</button>
            <span>{{ item.quantity }}</span>
            <button class="p-adj" @click="updateQuantity(index, item.quantity + 1)">‚ñ∂</button>
            <button class="remove-button" @click="removeFromCart(index)">ÁßªÈô§</button>
          </div>
        </span>
      </li>
    </ul>
    <hr />
    <h4 v-if="cart.length > 0" class="total-price">Á∏ΩË®àÈáëÈ°ç : {{ totalPrice }} NTD</h4>
    <div v-if="!(cart.length === 0)" class="order-area">
      <router-link :to="'/checkorder'" class="cart-order">Ë®ÇÂñÆÁµêÂ∏≥</router-link>
    </div>
  </div>
</template>

<script>
import { computed } from 'vue';
import { useStore } from 'vuex';  // ÂºïÂÖ• vuex store

export default {
  setup() {
    const store = useStore(); // ‰ΩøÁî® store
    const cart = computed(() => store.state.cart);  // Âæû Vuex store ÂèñÂæóË≥ºÁâ©ËªäË≥áÊñô

    // Ë®àÁÆóÁ∏ΩÂÉπÔºàËÄÉÊÖÆÊï∏ÈáèÔºâ
    const totalPrice = computed(() =>
      cart.value.reduce((sum, item) => sum + item.price * item.quantity, 0)
    );

    // Êõ¥Êñ∞Êï∏Èáè
    const updateQuantity = (index, newQuantity) => {
      const updatedCart = [...cart.value];

      if (newQuantity <= 0) {
        updatedCart.splice(index, 1); // Êï∏ÈáèÁÇ∫ 0 ÊôÇÁßªÈô§
      } else {
        updatedCart[index].quantity = newQuantity;
      }

      // ‰ΩøÁî® Vuex Êèê‰∫§Êõ¥Êñ∞
      store.commit('updateCart', updatedCart);
      localStorage.setItem('cart', JSON.stringify(updatedCart)); // Â≠òÂõû localStorage
    };

    // ÁßªÈô§Ë≥ºÁâ©ËªäÂïÜÂìÅÔºàÊï¥ÂÄãÂìÅÈ†ÖÔºâ
    const removeFromCart = (index) => {
      const updatedCart = [...cart.value];
      updatedCart.splice(index, 1);

      // ‰ΩøÁî® Vuex Êèê‰∫§Êõ¥Êñ∞
      store.commit('updateCart', updatedCart);
      localStorage.setItem('cart', JSON.stringify(updatedCart)); // Â≠òÂõû localStorage
    };

    return { cart, updateQuantity, removeFromCart, totalPrice };
  },
};
</script>
