    showMore: Boolean,  // ÊòØÂê¶È°ØÁ§∫„ÄåÊü•ÁúãÊõ¥Â§ö„ÄçÊåâÈàï
  },
  setup(props) {
    // ÈôêÂà∂È°ØÁ§∫ÁöÑÊñ∞ËÅûÊï∏Èáè
    const displayedNews = computed(() => {
      return props.limit ? props.news.slice(0, props.limit) : props.news;
