<template>
   <div>
     <div class="order-progress">
       <div class="progress-label" :class="{'active': step >= 1}">STEP1 : Á¢∫Ë™çË®ÇÂñÆ</div>
       <div class="right-icon">
        <font-awesome-icon :icon="['fas', 'right-long']" />
       </div>
       <div class="progress-label" :class="{'active': step >= 2}">STEP2 : Âª∫Á´ãË®ÇÂñÆ</div>
       <div class="right-icon">
        <font-awesome-icon :icon="['fas', 'right-long']" />
       </div>
       <div class="progress-label" :class="{'active': step >= 3}">STEP3 : ÂÆåÊàêË®ÇÂñÆ</div>
     </div>

     <!-- È°ØÁ§∫Ë≥ºÁâ©ËªäÂïÜÂìÅ -->
     <div v-if="step === 1" class="order-confirm">
       <h2>Á¢∫Ë™çÊÇ®ÁöÑË®ÇÂñÆ</h2>
       <!-- Ë≥ºÁâ©ËªäË≥áÊñô -->
       <div class="order-info">
        <div class="cart-info">
        <h3>Ë≥ºÁâ©ËªäË≥áË®ä</h3>
        <p v-if="cart.length === 0" class="warning-text">Ë´ãÂä†ÂÖ•ÂïÜÂìÅÊâçËÉΩÁπºÁ∫åÁµêÂ∏≥„ÄÇ</p>
       <div class="order-list">
         <ul class="order-items">  
          <li v-for="(item, index) in cart" :key="item.id" class="order-item">
           <img :src="item.image" class="item-img" />
           <span class="item-info">
              {{ item.name }} - {{ item.price }} NTD x {{item.quantity}}= {{ item.price * item.quantity }} NTD
              <div class="quantity-controls">
                <button class="p-adj" @click="updateQuantity(index, item.quantity - 1)">‚óÄ</button>
                <span>{{ item.quantity }}</span>
                <button class="p-adj" @click="updateQuantity(index, item.quantity + 1)">‚ñ∂</button>
                <button class="remove-button" @click="removeFromCart(index)">ÁßªÈô§</button>
              </div>
            </span>
            </li>
          </ul>
        </div>
        <h3 class="order-total">Á∏ΩË®àÔºö{{ totalPrice }} NTD</h3>
       </div>
       <!-- Ë®ÇË≥º‰∫∫Ë≥áÊñô -->
       <div class="recipient-info">
          <h3>Ë´ãËº∏ÂÖ•Ë®ÇË≥º‰∫∫Ë≥áË®ä</h3>
          <form @submit.prevent="submitOrder" ref="orderForm" class="order-form" novalidate>
            <div class="form-row">
              <label for="email"><span>*</span>‰ø°ÁÆ±</label>
              <input type="text" id="email" v-model="user.email" placeholder="Ë´ãËº∏ÂÖ•Email" required />
              <span v-if="errors.email" class="error-text">{{ errors.email }}</span>
            </div>
            <div class="form-row">
              <label for="name"><span>*</span>ÂßìÂêç</label>
              <input type="text" id="name" v-model="user.name" placeholder="Ë´ãËº∏ÂÖ•ÂßìÂêç" required />
            </div>
            <div class="form-row">
              <label for="address"><span>*</span>Âú∞ÂùÄ</label>
              <input type="text" id="address" v-model="user.address" placeholder="Ë´ãËº∏ÂÖ•Âú∞ÂùÄ" required />
            </div>
            <div class="form-row">
              <label for="phone"><span>*</span>ËÅØÁµ°ÈõªË©±</label>
              <input type="text" id="phone" v-model="user.phone" placeholder="Ë´ãËº∏ÂÖ•ÊâãÊ©üËôüÁ¢º" required />
              <span v-if="errors.phone" class="error-text">{{ errors.phone }}</span>
            </div>
            <div class="form-row">
              <label for="note">ÂÇôË®ª</label>
              <textarea id="note" rows="10" v-model="user.note" placeholder="Ë´ãËº∏ÂÖ•ÂÖ∂‰ªñË®ÇÂñÆÂÇôË®ªÈúÄÊ±Ç"></textarea>
            </div>
          </form>
       </div>
       </div>
       <div class="button-group">
         <button 
           @click="validateForm" :disabled="cart.length === 0" class="next-btn">
           ‰∏ã‰∏ÄÊ≠•
         </button>
       </div>
     </div>

     <!-- Áî®Êà∂Ë≥áÊñôËº∏ÂÖ• -->
     <div v-if="step === 2" class="order-create">
      <div class="order-create-info">
        <div class="cart-info-check">
       <div class="order-list">
         <ul class="order-items">  
          <li v-for="(item) in cart" :key="item.id" class="order-item">
           <img :src="item.image" class="item-img" />
            <span class="item-info">
              {{ item.name }} - {{ item.price }} NTD x {{item.quantity}}= {{ item.price * item.quantity }} NTD
            </span>
            </li>
          </ul>
        </div>
        <h3 class="order-total">Á∏ΩË®àÔºö{{ totalPrice }} NTD</h3>
       </div>
        <div class="recipient-info-check">
            <h3>ÊÇ®Ëº∏ÂÖ•ÁöÑË®ÇË≥º‰∫∫Ë≥áË®ä</h3>
            <div class="form-row">
              <label for="email"><span>*</span>‰ø°ÁÆ±</label>
              {{ user.email }}
            </div>
            <div class="form-row">
              <label for="name"><span>*</span>ÂßìÂêç</label>
              {{ user.name }}
            </div>
            <div class="form-row">
              <label for="address"><span>*</span>Âú∞ÂùÄ</label>
              {{ user.address }}
            </div>
            <div class="form-row">
              <label for="phone"><span>*</span>ËÅØÁµ°ÈõªË©±</label>
              {{ user.phone }}
            </div>
            <div class="form-row">
              <label for="note">ÂÇôË®ª</label>
              {{ user.note }}
            </div>
          </div>
      </div>
      <div class="button-group">
         <button class="prev-btn" @click="prevStep">‰∏ä‰∏ÄÊ≠•</button>
         <button 
           @click="orderestablish"  class="next-btn">‰∏ã‰∏ÄÊ≠•</button>
      </div>
     </div>

     <!-- Ë®ÇÂñÆÂÆåÊàê -->
     <div v-if="step === 3" class="order-complete">
       <h2>Ë®ÇÂñÆÂ∑≤ÂÆåÊàêÔºÅ</h2>
       <p>ÊÑüË¨ùÊÇ®ÁöÑË≥ºË≤∑ÔºåÊÇ®ÁöÑË®ÇÂñÆ{{order.orderNumber}}Â∑≤Á∂ìÂª∫Á´ãÂÆåÊàêÔºÅ</p>
       <p>ÊàëÂÄëÊúÉÂÑòÂø´ËôïÁêÜÊÇ®ÁöÑË®ÇÂñÆ‰∏¶ÁôºÈÄÅÂà∞ÊÇ®ÁöÑÂú∞ÂùÄ„ÄÇ</p>
       <div class="button-group">
        <button class="prev-btn" @click="prevStep">‰∏ä‰∏ÄÊ≠•</button>
        <button class="back-btn" @click="resetOrder">‰∫ÜËß£ËøîÂõû</button>
       </div>
     </div>
   </div>
</template>

<script>
import { mapState, mapMutations, mapGetters } from 'vuex';

export default {
   data() {
     return {
       step: 1,
       user: {
         email: '',
         name: '',
         address: '',
         phone: '',
         note: ''
       },
       order: {
       },
       errors: {
       }
     };
   },
   computed: {
     ...mapState(['cart']),
     ...mapGetters(['totalPrice'])
   },
   methods: {
     ...mapMutations(['updateCart', 'removeFromCart']),
     
     // Êõ¥Êñ∞Êï∏ÈáèÁöÑÂáΩÊï∏
     updateQuantity(index, newQuantity) {
       const updatedCart = [...this.cart];

       if (newQuantity <= 0) {
         updatedCart.splice(index, 1); // ÁßªÈô§Êï∏ÈáèÁÇ∫ 0 ÁöÑÂïÜÂìÅ
       } else {
         updatedCart[index].quantity = newQuantity;
       }

       this.updateCart(updatedCart);  // Êõ¥Êñ∞ Vuex ‰∏≠ÁöÑË≥ºÁâ©Ëªä
       localStorage.setItem('cart', JSON.stringify(updatedCart));  // ÂêåÊ≠•Âà∞ localStorage
     },

      // ÁßªÈô§Ë≥ºÁâ©ËªäÂïÜÂìÅÔºàÊï¥ÂÄãÂìÅÈ†ÖÔºâ
      removeFromCart(index) {
        const updatedCart = [...this.cart];
        updatedCart.splice(index, 1);
        // ‰ΩøÁî® Vuex Êèê‰∫§Êõ¥Êñ∞
        this.$store.commit('updateCart', updatedCart);
        localStorage.setItem('cart', JSON.stringify(updatedCart)); // Â≠òÂõû localStorage
     },


     nextStep() {
       if (this.cart.length > 0) {
         if (this.step < 3) {
           this.step++;
         }
       }
     },
     prevStep() {
       if (this.step > 1) {
         this.step--;
       }
     },
     validateForm() {
       const form = this.$refs.orderForm; // ÂèñÂæóË°®ÂñÆÂÖÉÁ¥†
       // **ÂÖàËÆì HTML5 Ë°®ÂñÆÈ©óË≠âÈÅã‰Ωú**
       if (!form.checkValidity()) {
           form.reportValidity(); // ËÆìÁÄèË¶ΩÂô®È°ØÁ§∫ÂÖßÂª∫È©óË≠âË®äÊÅØ
           return;
       }
       // **Ê∏ÖÁ©∫ÈåØË™§Ë®äÊÅØ**
       this.errors = {};
       let isValid = true;
       // ÊâãÂãïÈ©óË≠â Email
       if (!/\S+@\S+\.\S+/.test(this.user.email)) {
          this.errors.email = "Ë´ãËº∏ÂÖ•ÊúâÊïàÁöÑÈõªÂ≠êÈÉµ‰ª∂";
          isValid = false;
       }
       // ÊâãÂãïÈ©óË≠âÊâãÊ©üËôüÁ¢ºÊ†ºÂºè
       if (!/^09\d{8}$/.test(this.user.phone)) {
          this.errors.phone = "Ë´ãËº∏ÂÖ•ÊúâÊïàÊâãÊ©üËôüÁ¢ºÔºà09ÈñãÈ†≠Ôºå10Á¢ºÔºâ";
          isValid = false;
       }
       // **Â¶ÇÊûúÈ©óË≠âÊàêÂäüÔºåÊâçÈÄ≤ÂÖ•‰∏ã‰∏ÄÊ≠•**
       if (isValid) {
          this.nextStep();
        }
       },
       resetOrder() {
          this.step = 1;
          this.user = { name: '', address: '', phone: '' };
          this.updateCart([]); // Ê∏ÖÁ©∫ Vuex ‰∏≠ÁöÑË≥ºÁâ©ËªäË≥áÊñô
          localStorage.removeItem('cart'); // Ê∏ÖÁ©∫ localStorage ‰∏≠ÁöÑË≥ºÁâ©ËªäË≥áÊñô
        },
        generateOrderNumber() {
          const timestamp = Date.now(); // ÂèñÂæóÁï∂ÂâçÊôÇÈñìÊà≥
          const randomPart = Math.floor(1000 + Math.random() * 9000); // Áî¢ÁîüÂõõ‰ΩçÊï∏Èö®Ê©üÊï∏
          return `BBS-${timestamp}-${randomPart}`; // ÁµÑÂêàË®ÇÂñÆËôü
        },
        orderestablish() {
          this.order = {
          orderNumber: this.generateOrderNumber(), // Ë®ÇÂñÆÁ∑®Ëôü
          user: { ...this.user }, // Ë®ÇË≥º‰∫∫Ë≥áË®ä
          cart: [...this.cart], // Ë≥ºÁâ©ËªäÂÖßÂÆπ
          totalPrice: this.totalPrice, // Ë®ÇÂñÆÁ∏ΩÈáëÈ°ç
          createdAt: new Date().toISOString() // Ë®ÇÂñÆÂª∫Á´ãÊôÇÈñì
        };
        console.log("Ë®ÇÂñÆÂª∫Á´ãÊàêÂäüÔºö", this.order);
        // ÂÑ≤Â≠òÂà∞ localStorageÔºàÊ®°Êì¨Â≠òÂÑ≤Ôºâ
        // localStorage.setItem("order", JSON.stringify(this.order));
        // ÈÄ≤ÂÖ•‰∏ã‰∏ÄÊ≠•
        this.nextStep();
    }
   }
};
</script>
